.a{
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left{
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-right{
    flex:1 1;
    padding-right: 1%;
}

.a-img.bg{
    width: 30vw;
    height: 38vw;
    position: absolute;
    margin-top: -8vh;
    margin-left: -5vw;
    background-color: #555;
    border-width: 30px;
    border-radius: 30px;
    z-index: -1;
}

.a-img{
    width: 30vw;
    object-fit: cover;
    border-width: 30px;
    border-radius: 30px;
}

.a-title{
    font-weight: 400;
    font-size: 3vw;
}
.a-sub{
    margin: 20px 0px;
    font-size: 1.5vw;
}

.a-desc{
    font-weight: 300;
    font-size: 1.1vw;
}

.a-cat{
    margin-top: 5vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.a-cat-text{
    font-size: 1.1vw;
    margin-right: 10vw;
}

.a-cat-title{
    font-weight: bold;
    color: #555;
    font-size: 1.5vw;
}

.a-cat-img{
    width: 8vw;
    height: 8vw;
    object-fit: cover;
    border-radius: 20px;
}

@media screen and (max-width: 550px) {
    .a{
        flex-direction: column;
        margin-top: 10vw;
    }

    .a-img.bg{
        display: none;
    }

    .a-left{
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    .a-img{
        width: 70%;
        object-fit: cover;
    }

    .a-card{
        height: 30vh;
    }

    .a-card.bg, .a-cat{
        display: none;
    }

    .a-right{
        padding: 20px;
    }
    
    .a-title{
        font-size: 8vw;
        text-align: center;
    }

    .a-sub{
        font-size: 3.5vw;
    }

    .a-desc{
        font-size: 2.5vw;
    }


}
.i{
    margin-left: auto;
    margin-left: auto;
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1 1;
    display: flex;
    justify-content: center;
}

.i-left-wrapper{
    margin-top: 8vh;
    padding-left: 4vw;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.i-intro{
    font-size: 2vw;
    font-weight: 300;
}

.i-name{
    font-size: 5vw;
    font-weight: 700;
    white-space: nowrap;
}

.i-title{
    height: 6.5vh;
    overflow: hidden;
}

.i-desc{
    padding-top: 2vh;
    width: 100%;
    font-size: 1.3vw;
}

.i-title-wrapper{
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
    25%{
        -webkit-transform: translateY(-6vh);
                transform: translateY(-6vh);
    }
    50%{
        -webkit-transform: translateY(-12vh);
                transform: translateY(-12vh);
    }
    75%{
        -webkit-transform: translateY(-18vh);
                transform: translateY(-18vh);
    }
    100%{
        -webkit-transform: translateY(-24vh);
                transform: translateY(-24vh);
    }
}

@keyframes move {
    25%{
        -webkit-transform: translateY(-6vh);
                transform: translateY(-6vh);
    }
    50%{
        -webkit-transform: translateY(-12vh);
                transform: translateY(-12vh);
    }
    75%{
        -webkit-transform: translateY(-18vh);
                transform: translateY(-18vh);
    }
    100%{
        -webkit-transform: translateY(-24vh);
                transform: translateY(-24vh);
    }
}

.i-title-item{
    height: 6vh;
    font-size: 2vw;
    font-weight: bold;
    color: #228b22;
    display: flex;
    align-items: center;
}

.i-right{
    flex: 1 1;
    display:flex;
    flex-direction: column;
    justify-content:flex-end;
    align-items: center;
    padding: 50px;
}

.i-card{
    overflow: hidden;
}

.i-img{
    width: 45vw;
    border-style: double;
    border-color: forestgreen;
    border-width: 10px;
    border-radius: 30px;
    object-fit: contain;
}

.i-buttons{
    display:flex;
    justify-content: space-between;
    object-fit: contain;
    width: 30vw;
    padding-bottom: 3vh;
}

.al{
    object-fit: contain;
}

.ag{
    object-fit: contain;
}

.i-linkedin{
    width: 7vw;
    transition: all 0.2s ease-in-out;
}

.i-linkedin:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.i-github{
    width: 7vw;
    transition: all 0.2s ease-in-out;
}

.i-github:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


@media screen and (max-width: 550px) {
    .i{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .i-intro{
        font-size: 4vw;
    }

    .i-name{
        font-size: 10vw;
        justify-content: center;
        align-items: center;
    }

    .i-title-item{
        font-size: 4vw;
    }

    .i-left-wrapper{
        display: flex;
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 90%;
        width: 100vw;
    }

    .i-right{
        align-items: center;
        justify-content: center;
    }

    .i-img{
        width: 80vw;
    }

    .i-buttons{
        width: 60vw;
    }

    .i-linkedin{
        width: 20vw;
    }

    .i-github{
        width: 20vw;
    }

    .i-title-item{
        height: 6vh;
        align-items: center;
        justify-content: center;
    }

    .i-desc{
        display: none;
    }
}


