.video-box {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    overflow: hidden;
  }
  
  .thumbnail {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 1;
    transition: opacity 0.3s; /* Fade in the video over 0.3 seconds */
  }
  
  .video {
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0; /* Hide the video */
    transition: opacity 0.3s; /* Fade in the video over 0.3 seconds */
  }
  
  .video-box:hover .thumbnail {
    opacity: 0; /* Fade out the thumbnail on hover */
  }
  
  .video-box:hover .video {
    opacity: 1; /* Fade in the video on hover */
  }
  
.p{
    width: 100%;
    height: 45vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.p-card{
    width: 85%;
    height: 85%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3); 
}

.p-left {
    flex: 1 1;
    order: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 2vw;
}

.p-right {
    flex: 1 1;
    order: 2;
    display: flex;
    align-items: center;
    padding-right: 2vw;
}

.p-parent{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.p-box{
    width: 35vw;
    border: solid rgb(238, 231, 231) 3px;
    border-radius: 10px 10px 0px 0px;
    justify-content: center;
    align-items: center;
}

.p-browser{
    height: 2.3vh;
    background-color: rgb(238, 231, 231);
    align-items: center;
    display: flex;
}


.p-circle{
    width: 8px;
    height: 8px;
    background-color: white;
    border-radius: 50%;
    margin: 3px;
}

.p-texts-right{
    width: 100%;
    padding: 2% 5%;
    text-align: left;
}

.p-texts-left{
    width: 100%;
    padding-right: 1%;
    text-align: left;
}

.p-title{
    padding-top: 15px;
    width: 100%;
    font-size: 2vw;
    font-weight: 500;
}

.p-desc{
    font-size: 1.2vw;
    font-weight: 300;
    padding: 1vh 0% 0% 0%;
}

.p-subtitle{
    width: 100%;
    font-size: 1.5vw;
    font-weight: 450;
    padding: 2vh 0% 0% 0%;
}

@media screen and (max-width: 550px) {
