        height: 100%;
    }
    .p-card{
        margin: 5%;
        width: 100%;
        height: 100%;
        flex-direction: column;
    }
    .p-left{
        padding: 0;
        order: 1;
        width: 80%;
        height: 50%;
        justify-content: center;
    }
    .p-right{
        padding: 0;
        order: 1;
        width: 80%;
        height: 50%;
        justify-content: center;
    }
    .p-parent{
        width: 100%;
    }
    .p-box{
        position: relative;
        margin-top: 5%;
        width: 100%;
        height: 100%;
    }
    .p-texts-right{
